import { Fragment, useState } from "react";
import TwoLineContainer from "./TwoLineContainer";
import DeleteButton from "./DeleteButton";
import { data } from "../data/stations";
import DropBox from "./DropBox";

function DetailStation() {
  const StationInfoTable = (props) => {
    return (
      <table className="detailStation-table">
        <thead>
          <tr>
            <td>스테이션 번호</td>
            <td>스테이션 명</td>
            <td>설치 주소</td>
            <td>설치일자</td>
            <td>설치장소 사진</td>
            <td>설치업체</td>
            <td>운영상태</td>
          </tr>
        </thead>
        <tbody>
          <tr>
            <StationInfoChange {...props} />
          </tr>
        </tbody>
      </table>
    );
  };

  const StationInfoChange = (props) => {
    if (props.editMode === false) {
      return (
        <>
          {data.map((value, index) => {
            return (
              <Fragment key={index}>
                <td>{value.stationNumber}</td>
                <td>{value.stationName}</td>
                <td>{value.installAddress}</td>
                <td>{value.installDate}</td>
                <td>{value.installPicture}</td>
                <td>{value.installCompany}</td>
                <td>{value.state}</td>
              </Fragment>
            );
          })}
        </>
      );
    } else if (props.editMode === true) {
      return (
        <>
          <td className="active">
            <input defaultValue={data[0].stationNumber}></input>
          </td>
          <td className="active">
            <input defaultValue={data[0].stationName}></input>
          </td>
          <td className="active">
            <input defaultValue={data[0].installAddress}></input>
          </td>
          <td className="active">
            <input defaultValue={data[0].installDate}></input>
          </td>
          <td className="active">
            <div className="tiny-button">등록</div>
            <div className="tiny-button">삭제</div>
          </td>
          <td className="active">
            <input defaultValue={data[0].installCompany}></input>
          </td>
          <td className="active">
            <DropBox defaultIndex={data[0].state}>
              <div>운용 중</div>
              <div>철거</div>
            </DropBox>
          </td>
        </>
      );
    } else {
      return null;
    }
  };

  const StationTypeTable = (props) => {
    const [stationType] = useState("water");
    const Th = () => {
      if (stationType === "water") {
        return (
          <tr>
            <td>발전방식</td>
            <td>SMPS 스펙</td>
          </tr>
        );
      } else if (stationType === "sun") {
        return (
          <tr>
            <td>발전방식</td>
            <td>패널 스펙</td>
            <td>배터리 스펙</td>
          </tr>
        );
      }
    };

    return (
      <table className="detailStation-table">
        <thead>
          <Th />
        </thead>
        <tbody>
          <tr>
            <StationTypeChange {...props} stationType={stationType} />
          </tr>
        </tbody>
      </table>
    );
  };

  const StationTypeChange = (props) => {
    if (props.stationType === "water") {
      if (props.editMode === false) {
        return (
          <tr>
            <td>수전</td>
            <td>12V 30A</td>
          </tr>
        );
      } else if (props.editMode === true) {
        return (
          <tr>
            <DropBox defaultIndex={0}>
              <div>수전</div>
              <div>태양광</div>
            </DropBox>
            <td>
              <input defaultValue="12V 30A"></input>
            </td>
          </tr>
        );
      }
    } else if (props.stationType === "sun") {
      if (props.editMode === false) {
        return (
          <tr>
            <td>태양광</td>
            <td>420W</td>
            <td>4800W</td>
          </tr>
        );
      } else if (props.editMode === true) {
        return (
          <tr>
            <DropBox defaultIndex={0}>
              <div>수전</div>
              <div>태양광</div>
            </DropBox>
            <td>
              <input defaultValue="420W"></input>
            </td>
            <td>
              <input defaultValue="4800W"></input>
            </td>
          </tr>
        );
      }
    }
  };

  const StationPortTable = (props) => {
    return (
      <table className="detailStation-table">
        <thead>
          <tr>
            <td>발전방식</td>
            <td>SMPS 스펙</td>
          </tr>
        </thead>
        <tbody>
          <tr>
            <StationInfoChange {...props} />
          </tr>
        </tbody>
      </table>
    );
  };

  return (
    <div className="detailStation-container">
      <div style={{ marginBottom: "20px" }}>
        <TwoLineContainer
          hasImg="edit"
          title="스테이션 정보"
          className="stationInfo-container"
        >
          <StationInfoTable />
        </TwoLineContainer>
      </div>
      <div style={{ marginBottom: "20px" }}>
        <TwoLineContainer
          hasImg="edit"
          title="발전 정보"
          className="stationType-container"
        >
          <StationTypeTable />
        </TwoLineContainer>
      </div>
      <div>
        <TwoLineContainer
          hasImg="edit"
          title="포트 정보"
          className="stationPort-container"
        >
          <StationPortTable />
        </TwoLineContainer>
      </div>
      <DeleteButton />
    </div>
  );
}

export default DetailStation;
