import AddButton from "../components/AddButton";
import RadioStationType from "./RadioStationType";
import PortInfo from "./PortInfo";

function AddStation() {
  return (
    <div className="addStation-container">
      <div className="addStation-title">스테이션 등록</div>
      <form className="addStation-form">
        <label htmlFor="stationName">스테이션 명</label>
        <input className="longInput" id="stationName"></input>
        <label htmlFor="installDate">설치 일자</label>
        <input className="longInput" id="installDate"></input>
        <label htmlFor="installAddress">설치 주소</label>
        <input className="longInput" id="installAddress"></input>
        <label htmlFor="addPicture">사진 등록</label>
        <input className="longInput" id="addPicture"></input>
        <label htmlFor="installCompany">설치 업체</label>
        <input className="longInput" id="installCompany"></input>
        <RadioStationType />
        <PortInfo />
        <AddButton />
      </form>
    </div>
  );
}

export default AddStation;
