import { Fragment, useReducer } from "react";
import addButtonIcon from "../images/plus_button.png";
import deleteButtonIcon from "../images/minus_button.png";
import eightMMIcon from "../images/8mm.png";
import sixteenMMIcon from "../images/16mm.png";
import DropBox from "./DropBox";
import { data } from "../data/portList";

function PortInfo() {
  const [, forceUpdate] = useReducer((x) => x + 1, 0);

  const PortList = () => {
    return (
      <Fragment>
        {data.map((value, index) => {
          return (
            <div className="portList-container">
              <div>
                <label htmlFor="portNumber">포트넘버</label>
                <input
                  className="shortInput"
                  id="portNumber"
                  defaultValue={value.portNumber}
                />
              </div>
              <div>
                <label>단자모양</label>
                <DropBox defaultIndex={value.portShape}>
                  <div>
                    <img src={eightMMIcon} alt="eightMMIcon"></img>
                    DC 8mm
                  </div>
                  <div>
                    <img src={sixteenMMIcon} alt="sixteenMMcon"></img>
                    항공 16mm
                  </div>
                </DropBox>
              </div>
              <div>
                <label htmlFor="QR_value">QR 코드 값</label>
                <input
                  className="shortInput"
                  id="QR_value"
                  defaultValue={value.qrCode}
                />
              </div>
            </div>
          );
        })}
      </Fragment>
    );
  };

  const Buttons = () => {
    return (
      <div className="portInfoButton-container">
        <img
          src={addButtonIcon}
          onClick={() => {
            data.push({ portNumber: data.length + 1 });
            forceUpdate();
          }}
        ></img>
        <img
          src={deleteButtonIcon}
          onClick={() => {
            data.pop();
            forceUpdate();
          }}
        ></img>
      </div>
    );
  };

  return (
    <div className="portInfo-container">
      <Buttons />
      <PortList />
    </div>
  );
}

export default PortInfo;
